@charset "UTF-8";

// Archive (e.g., category pages), search results, author page styles.

.archive,
.search-results {
  main > section {
    > .post {
      border-bottom: 2px solid get-color(text);

      &:not(:last-of-type) {
        @include modular-spacing("margin", null null 5);
      }

      &:last-of-type {
        @include modular-spacing("margin", null null 1);
      }

      .img--featured {
        @include modular-spacing("margin", null null 0);
      }

      > *:last-child:not(.tags) {
        @include modular-spacing("padding", null null 1);
      }

      > .tags {
        @include margin(2rem null null);
        border: 0;
      }
    }

    h1 {
      @include modular-spacing("margin", null null 3);
    }
  }
}

.archive .post > header h2,
.search-results .post > header h2,
.author .post > header h3 {
  @include type-a;

  a {
    text-decoration: none;

    &:hover,
    &:focus {
      text-decoration: underline;
    }
  }
}
