@charset "UTF-8";

// SASS color maps for the Redwood theme. This overrides the Decanter colors.

// Main color palette
$rw-colors: (
    brand:                        $su-color-cardinal-red,
    brand--bright:                $su-color-bright-red,
    brand--dark:                  $su-color-dark-red,
    link:                         $su-color-bright-blue,
    link--hover:                  $su-color-black,
    link--alt:                    $su-color-bright-red,
    link--alt-hover:              $su-color-black,
    link--dark:                   $su-color-black,
    link--dark-hover:             $su-color-bright-blue,
    link--reverse:                $su-color-white,
    link--reverse-hover:          $su-color-vivid-red,
    link--reverse-alt:            #6fc3ff,
    link--reverse-alt-hover:      $su-color-white,
    bg:                           $su-color-white,
    bg--alt:                      $su-color-fog,
    bg--dark:                     $su-color-chocolate,
    border:                       $su-color-driftwood,
    text:                         $su-color-black,
    text--high-contrast:          #000,
    text--alt:                    $su-color-bright-red,
    text--light:                  $su-color-clay,
    text--reverse:                $su-color-white,

  //
  // ------------------------------------
  // Page region colors
  // ------------------------------------
  //
     prefooter-bg:                $su-color-fog,
     prefooter-bg--dark:          $su-color-chocolate,
     prefooter-link:              $su-color-bright-red,
     prefooter-link--hover:       $su-color-black,

  //
  // ------------------------------------
  // Widget colors
  // ------------------------------------
  //
     widget-bg--sidebar:           $su-color-white,
     widget-header:                $su-color-cardinal-red,
     widget-header--alt:           $su-color-black,

     // Tags
     tag-bg:                       $su-color-clay,
     tag-bg--hover:                $su-color-bright-red,
     tag-text:                     $su-color-white,

     // Social icons
     social-icon:                  $su-color-black,

  //
  // ------------------------------------
  // Elements
  // ------------------------------------
  //
     accent-stroke:                $su-color-bright-red,
     thin-line:                    $su-color-cool-grey-25,
     post-separator:               $su-color-driftwood,
     sticky-bg:                    $su-color-light-sandstone,
     button--one:                  $su-color-mint,
     button--two:                  $su-color-lagunita,
     button--three:                $su-color-cool-grey,
     button--primary:              $su-color-cardinal-red,
     caption:                      $su-color-cool-grey,
     quotes:                       #e0e0e0,
     pre-bg:                       $su-color-light-sandstone,
);

// Social media brand colors
$social-brands: (
     'Facebook':                    #3b579d,
     'Twitter':                     #1da1f2,
     'Instagram':                   #d73676,
     'LinkedIn':                    #0077b5,
     'YouTube':                     #cd201f,
     'iTunes U':                    #a3aaae,
     'Reddit':                      #ff5700,
);
