@charset "UTF-8";

// Styles for article images (both Gutenberg and classic editor), gallery, Gutenberg cover image etc
// Featured image styles can be found in layout/_post.scss

//
// ------------------------------------
// Article Images
// ------------------------------------
//
.wp-block-image,
.wp-block-image figure,
article > figure {
  @include margin(1.4em null);
}

figure.wp-block-image,
figure.wp-block-image.is-resized {
  &,
  img {
    @include margin(null auto);
  }
}

.wp-block-image img {
  @include margin(null auto);
}

//  Image links
a {
  &:hover,
  &:focus {
    img {
      opacity: 0.74;
      transition: opacity 0.3s ease-out;
    }
  }
}

// IE only fix to prevent full size image overflow
@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
  .wp-block-image img,
  .wp-caption img {
    width: 100%;
  }
}

// left or right align starts at LG breakpoint
.wp-block-image .alignright,
.wp-block-image .alignleft,
figure.alignright,
figure.alignleft {
  @include grid-media-max('md') {
    @include margin(null auto);
    float: none;
  }

  @include grid-media-min('lg') {
    @include margin(0.4em null 0.8em);
    max-width: 67%;
  }
}

// Classic editor images
.wp-caption > figcaption,
.post--type-attachment figcaption {
  @include margin(0.5em null null);
}

.post__content {
  figure:not(.alignright):not(.alignleft) {
    &:first-child {
      @include margin(0 null null);
    }
  }
}

//
// ------------------------------------
// Gutenberg Gallery
// ------------------------------------
//
.wp-block-gallery {
  &:not(.alignleft):not(.alignright) {
    @include margin(1.4em null 0.6em);
  }

  footer &,
  #sidebar--left &,
  #sidebar--right & {
    @include margin(null null -16px);
  }
}

.post__content .wp-block-gallery {
  @include padding(null null null 0);
}

.blocks-gallery-image,
.blocks-gallery-item {
  .wp-block-gallery & {
    width: calc((100% - 17px) / 2); // IE fix - needs 1px extra subtracted from 100%; this is the default columns-2

    figcaption {
      @include modular-typography(-1);
      @include linear-gradient(to top, rgba(0,0,0,0.9), rgba(0,0,0,0.42) 60%, rgba(0,0,0,0.2) 80%, transparent);
      @include padding(2em 0.6em 0.6em);
      max-width: 100%;
      font-weight: $su-font-semi-bold;
      text-shadow: rgba(0,0,0,0.8) 0 0 1px, rgba(0,0,0,0.6) 0 0 8px;

      a {
        color: get-color(link--reverse);
      }
    }
  }

  // IE fixes
  @media (min-width: 600px) {
    .wp-block-gallery.columns-3 & {
      width: calc((100% - 33px) / 3);
    }

    .wp-block-gallery.columns-4 & {
      width: calc((100% - 49px) / 4);
    }
  }

  .wp-block-gallery.is-cropped & {
    a,
    img {
      height: 100%;
    }
  }
}

//
// ------------------------------------
// Gutenberg Cover
// ------------------------------------
//
.wp-block-cover {
  @include modular-spacing('padding', 2 null);
}

.wp-block-cover__inner-container {
  p {
    @include modular-typography(1);
    font-weight: $su-font-bold;
    width: 100%; // IE needs this
    max-width: 100%;

    &.has-large-font-size {
      @include type-b;
      line-height: $su-displays-line-height;
    }

    > a {
      color: get-color(text--reverse);
      font-weight: $su-font-bold;
    }
  }

  > *:last-child {
    @include margin(null null 0);
  }
}

.wp-block-cover:not(.alignleft):not(.alignright) {
  @include margin(1.4em null);
}
