@charset "UTF-8";

// Styles for common classes generated by both classic and Gutenberg editors

figure,
.wp-block-media-text {
  @include margin(1.4em 0);
}

.aligncenter {
  @include margin(null auto);
  clear: both;
}

.alignright,
.alignleft {
  @include grid-media-max('md') {
    @include margin(null auto);
    float: none;
  }

  @include grid-media-min('lg') {
    @include margin(0.4em null 1em);
    max-width: 67%;
  }
}

@include grid-media-min('lg') {
  .alignright {
    @include margin(null 0 null null);
    float: right;
  }

  .alignleft {
    @include margin(null null null 0);
    float: left;
  }
}

a {
  &.alignright,
  &.alignleft,
  &.aligncenter {
    @include margin(0);
    display: block;
    float: none;
    max-width: 100%;
  }

  &.alignright {
    text-align: right;
  }

  &.alignleft {
    text-align: left;
  }

  &.aligncenter {
    text-align: center;
  }
}

.is-style-squared {
  border-radius: 0;
}

table {
  word-wrap: break-word; // IE
  word-break: break-word;
}
