@charset "UTF-8";

// Styles here are unique to the .tease class
// Styles common to single posts and tease posts can be found in _post.scss
// Post list shortcode styles (list of tease posts) can be found in elements/_shortcodes.scss

.tease {
  @include modular-spacing('margin', null null 1);
  @include modular-spacing('padding', null null 1);
  position: relative;
  clear: both;

  > * {
    align-self: start;
  }

  @include grid-media-max('sm') {
    header {
      > h2,
      > h3,
      > h4 {
        clear: none;
      }
    }

    .read-more {
      display: block;
      clear: both;
    }
  }

  @include grid-media-min('md') {
    display: grid;
    grid-template-columns: auto 1fr;
    grid-template-rows: auto auto 1fr;
    grid-template-areas: "tease-image tease-header" "tease-image tease-summary" "tease-image tease-readmore";

    > * {
      min-width: 0; // Prevent grid "blow out" https://css-tricks.com/preventing-a-grid-blowout/
    }

    header {
      > h2,
      > h3,
      > h4 {
        @include margin(-0.28em null null);
      }
    }
  }

  border-bottom: 1px solid get-color(post-separator);

  header {
    grid-area: tease-header;

    > h2,
    > h3,
    > h4 {
      @include modular-spacing('margin', null null -2);
      @include type-c;
    }
  }

  > .tease__img-link,
  > img {
    grid-area: tease-image;
    display: block;
    align-self: start; // IE needs this to not stretch image
  }

  img {
    @include grid-media-max('sm') {
      @include margin(0.4em null 0.4em 1em);
      width: 120px;
      float: right;
    }

    @include grid-media-min('md') {
      @include margin(null 1.25em null 0);
      width: 200px;
    }

    @include grid-media-only('md') {
      width: 18vw;
      max-width: 200px;
    }
  }

  .read-more {
    @include modular-spacing('margin', -1 null null);
    grid-area: tease-readmore;
    line-height: 1;

    @include grid-media-max("sm") {
      @include margin(null auto);
      text-align: center;
    }
  }

  > p,
  > section {
    grid-area: tease-summary;
    @include modular-spacing('margin', -1 null null);
    @include margin(null null 0);
  }

  &.sticky {
    @extend %accent-stroke;
    @include modular-spacing('padding', 1 null null);
    @include padding(null $su-gutter-xs null null);
    background: linear-gradient(-60deg, get-color(sticky-bg) 0, rgba(255,255,255,0) 90%);

    @include grid-media-only('xs') {
      @include padding(null map-get($su-screen-margins, 'xs'));
      width: calc(100% + #{map-get($su-screen-margins, 'xs') * 2});
      transform: translateX(- map-get($su-screen-margins, 'xs'));
    }

    @include grid-media-only('sm') {
      @include padding(null map-get($su-screen-margins, 'sm'));
      width: calc(100% + #{map-get($su-screen-margins, 'sm') * 2});
      transform: translateX(- map-get($su-screen-margins, 'sm'));
    }

    @include grid-media-min("lg") {
      header > h2 {
        font-size: 1.8em;
      }
    }
  }
}

.tease--custom {
  > section {
    > *:last-child {
      @include margin(null null 0);
    }
  }
}
