@charset "UTF-8";

// Unbranded site name and subheadline

.sitename {
  display: flex;
  flex-direction: column;
  text-decoration: none;
  max-width: fit-content;

  &,
  &:hover,
  &:focus {
    color: get-color(text);
  }

  > span {
    display: block;
  }

  > span:first-child {
    @include margin(null null 0.2em);
    @include serif;
    @include modular-typography(3);
    font-weight: $su-font-bold;
    letter-spacing: -0.016em;
  }

  > span:nth-child(2) {
    @include modular-typography(1);
    @include sans;
    font-weight: 300;
  }

  > span:last-child {
    @include margin(null null 0);
  }
}
