@charset 'UTF-8';

// Gutenberg and classic editor tables

table,
.wp-block-table {
  border-collapse: collapse;

  td {
    word-break: normal;
  }
}

table {
  caption {
    @include margin(null null 0.6em);
    font-weight: $su-font-bold;
    text-align: left;
  }

  thead {
    th {
      font-weight: $su-font-bold;
    }
  }
}

.wp-block-table {
  &.has-background {
    td {
      background-color: inherit;
    }
  }
}

.wp-block-table.is-style-stripes,
.table-striped {
  td {
    border: 0;
  }

  tr {
    border-bottom: 1px solid get-color(bg--alt);

    &:nth-child(odd) {
      background-color: get-color(bg--alt);

      td {
        background-color: transparent;
      }
    }
  }
}

.wp-block-table.is-style-stripes {
  border-bottom: 0;

  thead {
    border-top: 1px solid $su-color-fog;
  }

  table thead th,
  table thead td {
    background: $su-color-cool-grey-25;
  }
}

figure {
  &.wp-block-table {
    @include margin(0 null);
  }
}

.data-table {
  tr td:last-child {
    text-align: right;
  }
}

table caption,
.table-title {
  @include type-d;
}

.table-title {
  + .wp-block-table table,
  + table {
    @include margin(0 null null);
  }
}
