@charset "UTF-8";

.one-column:not(.left-nav) {
  main {
    @include grid-media-max('lg') {
      grid-template-columns: 1fr;
      grid-template-areas: "main";
    }

    @include grid-media-only('xl') {
      grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
      grid-template-areas: ". main main main main main main main main main main .";
    }

    @include grid-media-only('2xl') {
      grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
      grid-template-areas: ". . main main main main main main main main . .";
    }
  }

  @include grid-media-min('md') {
    .post__content,
    .post__comments {
      @include margin(null auto);
      width: 80%;
    }
  }

  .post {
    > header {
      text-align: center;
    }
  }
}

.left-nav.one-column {
  @include grid-media-max("sm") {
    main {
      grid-template-areas: "left-nav" "main";
    }
  }

  @include grid-media-between("md", "xl") {
    main {
      grid-template-columns: 1fr 1fr 1fr;
      grid-template-rows: auto 1fr;
      grid-template-areas: "left-nav main main" ". main main";
    }
  }

  @include grid-media-min("2xl") {
    main {
      grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
      grid-template-rows: auto 1fr;
      grid-template-areas: ". left-nav left-nav left-nav main main main main main main main ."
                           ". . . . main main main main main main main .";
    }
  }
}


