@charset "UTF-8";

main {
  @include grid-media-max("sm") {
    .two-column--right-sidebar & {
      grid-template-areas: "main" "sidebar-r";
    }

    .two-column--left-sidebar & {
      grid-template-areas: "main" "sidebar-l";
    }
  }

  @include grid-media-min("md") {
    .two-column--right-sidebar & {
      grid-template-areas: "main main sidebar-r";
      grid-template-columns: 1fr 1fr 1fr;
      grid-template-rows: auto;
      grid-column-gap: $su-gutter-md;
    }

    .two-column--left-sidebar & {
      grid-template-areas: "sidebar-l main main";
      grid-template-columns: 1fr 1fr 1fr;
      grid-template-rows: auto;
      grid-column-gap: $su-gutter-md;
    }
  }

  @include grid-media-min("lg") {
    .two-column--right-sidebar &,
    .two-column--left-sidebar & {
      grid-column-gap: $su-gutter-lg;
      -ms-grid-columns: 1fr $su-gutter-lg 1fr $su-gutter-lg 1fr;
    }
  }

  @include grid-media-min("xl") {
    .two-column--right-sidebar &,
    .two-column--left-sidebar & {
      grid-column-gap: $su-gutter-xl;
      -ms-grid-columns: 1fr $su-gutter-xl 1fr $su-gutter-xl 1fr;
    }
  }

  @include grid-media-min("2xl") {
    .two-column--right-sidebar & {
      grid-template-areas: ". main main main main main main main sidebar-r sidebar-r sidebar-r .";
      grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
      grid-column-gap: $su-gutter-2xl;
    }

    .two-column--left-sidebar & {
      grid-template-areas: ". sidebar-l sidebar-l sidebar-l main main main main main main main .";
      grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
      grid-column-gap: $su-gutter-2xl;
    }
  }
}

.two-column--left-sidebar,
.two-column--right-sidebar,
.three-column {
  main {
    > nav {
      @include modular-spacing("margin", null null 2);

      @include grid-media-max("sm") {
        @include margin(null null 0);
      }
    }
  }
}

.two-column--right-sidebar main {
  > nav {
    @include grid-media-min("xl") {
      @include margin(null null 0);
    }
  }
}
