@charset "UTF-8";

// Styles shared between WP calendar widget and the Gutenberg widget calendar block

.widget_calendar,
.wp-block-calendar {
  table {
    th,
    td {
      @include padding(0.5em 0);
      text-align: center;
    }

    th,
    tbody td,
    tfoot {
      border: 1px solid $su-color-driftwood;
    }
  }

  caption {
    @include types;
    @include type-d;
    @include margin(null null 0.6rem);
    text-align: center;
    color: get-color(text);
  }

  #today {
    background: get-color(brand--bright);

    &,
    a {
      color: get-color(text--reverse);
    }
  }
}

.widget_calendar {
  table {
    @include margin(0);
  }

  tfoot {
    td {
      border: 0;
    }
  }
}

.wp-block-calendar {
  @include margin(null auto);

  @include grid-media-min('md') {
    max-width: 75%;
  }

  table {
    @include sans;

    th {
      background-color: $su-color-cloud;
    }
  }

  tfoot {
    a {
      color: color(link);

      &:hover,
      &:focus {
        color: color(link--hover);
      }
    }
  }
}
