@charset "UTF-8";

body {
  @include font-smoothing;
}

main,
#pre-footer {
  color: get-color(text);
}

// Vertical position finetuning due to inherent line height
main > section > h1:first-of-type,
.home .post__content > h1:first-child {
  @include margin(-0.3em null null null);
}

#pre-footer,
aside {
  ul,
  ol,
  p {
    @include rw-small-paragraph;
  }
}

@include grid-media-narrow-sidebars {
  ul,
  ol,
  p {
    font-size: 1.6rem;
  }
}

address {
  line-height: $su-base-line-height;
}

abbr[title] {
  text-decoration: none; // normalize adds dotted line underneath
}

// FontAwesome support for legacy themes and Redwood
.fa.far {
  font-weight: 400;
}

p {
  max-width: 100%; // IE fix
  word-break: break-word;

  // WordPress dropcap
  &.has-drop-cap:not(:focus) {
    &:first-letter {
      @include serif;
      font-size: 5.2em;
      line-height: .8;
      font-weight: $su-font-regular;
    }

    &::after {
      @include padding(0 null null);
    }
  }

  &:empty {
    display:none;
  }
}

figcaption {
  line-height: 1.3;
}

article strong a {
  font-weight: 700;
}

// Use Decanter modular typography for WordPress custom font size levels
.has-medium-font-size {
  @include modular-typography(1);
}

.has-large-font-size {
  @include modular-typography(2);
}

.has-huge-font-size {
  @include modular-typography(3);
}