@charset "UTF-8";

// Tag Cloud Widget and Gutenberg block
.tag-cloud-link {
  @include margin(null 1rem 0.4rem null);
}

aside, #pre-footer {
  .tag-cloud-link {
    font-weight: $su-font-semi-bold;
  }
}

.widget_tag_cloud {
  @extend %widget-box;
  @extend %accent-stroke;

  .tagcloud {
    @include margin(null null -0.4rem); // To offset margin-bottom of last row of tags
  }
}

// Tags at the bottom of single posts
.post .tags {
  @include padding(2rem null 1rem);
  border-top: 1px solid get-color(thin-line);
  border-bottom: 1px solid get-color(thin-line);

  a {
    @include padding(0.7rem 1.8rem 0.8rem);
    @include margin(null 1.2rem 1rem null);
    font-size: 0.9em;
    color: get-color(tag-text);
    background: get-color(tag-bg);
    border-radius: 8rem;
    line-height: 1.1;
    text-decoration: none;

    &:hover,
    &:focus {
      background: get-color(tag-bg--hover);
      color: get-color(tag-text);
      text-decoration: underline;
    }

    &:first-of-type {
      @include margin(null null null 8px);
    }
  }
}

article .tags a,
.tag-cloud-link {
  display: inline-block;
}
