@charset "UTF-8";

.widget_well_widget,
.well {
  @extend %widget-box;
  @extend %accent-stroke;
  line-height: $su-base-line-height;

  > h2 {
    color: get-color(widget-header--alt);
    text-align: center;
    text-transform: none;
  }

  > h3 {
    @include type-d;
  }

  > h4 {
    @include type-e;
  }

  > h5 {
    @include type-f;
  }

  > h3,
  > h4,
  > h5,
  > h6 {
    @include margin(null null 0.6em);
  }

  > h2,
  > h3,
  > h4,
  > h5 {
    &:first-child {
      @include margin(0 null null);
    }
  }

  > .fa {
    @include modular-spacing('margin', null null -1);
    display: block;
    text-align: center;
  }
}

aside .widget_well_widget h2 {
  @include type-d;
}

.post__content .well {
  &:not(:last-child) {
    @include margin(null null 1.4em);
  }

  &:not(:first-child) {
    @include margin(1.4em null null);
  }
}