@charset 'UTF-8';

// Embed - videos, audios, third party content (e.g., Tweets, Instagram posts, Soundcloud etc.)

.wp-block-embed,
.wp-video {
  @include margin(null null 1.4em);
}

.wp-block-embed.is-type-video,
.wp-has-aspect-ratio {
  iframe {
    @include size(100%);
    max-width: 100%;
    position: absolute;
  }

  .wp-block-embed__wrapper {
    position: relative;
    height: 0;
    overflow: hidden;
  }
}

.wp-embed-aspect-16-9,
.is-type-video {
  .wp-block-embed__wrapper {
    padding-bottom: 56.25%; // 16:9
  }
}

.wp-embed-aspect-4-3,
.wp-block-embed-slideshare.is-provider-slideshare {
  .wp-block-embed__wrapper {
    padding-bottom: 75%; // 4:3
  }
}

.wp-embed-aspect-3-2 {
  .wp-block-embed__wrapper {
    padding-bottom: 66.67%; // 3:2
  }
}

.wp-embed-aspect-1-1 {
  .wp-block-embed__wrapper {
    padding-bottom: 100%; // 1:1
  }
}

// Classic editor self hosted video
.wp-video {
  @include margin(1.4em null null);
}

.wp-block-audio {
  @include margin(null auto);
  width: 100%;

  @include grid-media-min('sm') {
    &.aligncenter {
      width: 75%;
    }
  }
}

.wp-block-embed-wordpress.is-type-wp-embed {
  iframe {
    max-width: 100%;
  }
}

.wp-block-embed {
  @include grid-media-min('lg') {

    &-instagram,
    &-twitter,
    &-wordpress {
      &,
      &.aligncenter {
        max-width: 500px;
      }
    }

    &-reddit {
      max-width: 600px;
    }
  }
}