@charset 'UTF-8';

// Pre, Gutenberg Preformatted, Code, Verse

pre {
  @include margin(1.4em null);
  overflow-x: auto;
  line-height: $su-base-line-height;
}

.wp-block-code,
.wp-block-preformatted,
.wp-block-verse {
  @include modular-spacing('padding', 1);
  background-color: get-color(pre-bg);
  border-radius: 1rem;
  border: 1px solid $su-color-cloud;
}

pre.wp-block-verse {
  @include sans;
  white-space: pre-wrap;
}
