@charset "UTF-8";

// Style for the homepage banner region

.banner {
  @include margin(null null 0);

  > img {
    @include margin(null auto);
    max-height: 600px;
  }

  &.fullwidth {
    @include margin(0 null null);

    @include grid-media-only("lg") {
      overflow: hidden;
      height: 400px;
    }

    @include grid-media("xl") {
      height: 500px;
    }

    > img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: top;
    }

    > figcaption {
      @each $key, $breakpoint in $su-grid-media {
        @if index($su-grid-media, ($key $breakpoint)) > 3 { // Breakpoint LG and larger
          $_edge: map-get($su-screen-margins, $key);

          &.banner__text--bottom-left,
          &.banner__text--top-left {
            @include grid-media($key) {
              left: $_edge;
            }
          }

          &.banner__text--bottom-right,
          &.banner__text--top-right {
            @include grid-media($key) {
              right: $_edge;
            }
          }
        }
      }

      @media only screen and (min-width: #{$su-screen-2xl + map_get($su-screen-margins, '2xl') * 2}) {
        $_max_width_edge: calc((100% - #{$su-site-max-width}) / 2);

        &.banner__text--bottom-left,
        &.banner__text--top-left {
          left: $_max_width_edge;
        }

        &.banner__text--bottom-right,
        &.banner__text--top-right {
          right: $_max_width_edge;
        }
      }
    }
  }

  &.contentwidth {
    @include margin(null auto);
    @include modular-spacing("margin", 2 null null);

    @include grid-media-min("lg") {
      max-width: 1500px;
    }

    // At MD and smaller breakpoints, homepage banner renders the same for both fullwidth and content-width
    @include grid-media-max("md") {
      @include padding(null 0);
      @include margin(0 0 null);
      max-width: 100%;
      width: 100%;

      > img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: top;
      }
    }

    @include grid-media-only("lg") {
      max-width: calc(100% - 2 * #{map_get($su-screen-margins, 'lg')});
      width: calc(100% - 2 * #{map_get($su-screen-margins, 'lg')});
    }

    @include grid-media-only("xl") {
      max-width: calc(100% - 2 * #{map_get($su-screen-margins, 'xl')});
      width: calc(100% - 2 * #{map_get($su-screen-margins, 'xl')});
    }

    @media only screen and (min-width: $su-screen-2xl) and (max-width: #{$su-screen-2xl + map_get($su-screen-margins, '2xl') * 2}) {
      max-width: calc(100% - 2 * #{map_get($su-screen-margins, '2xl')});
    }

    @include grid-media-min("lg") {
      display: block;
      width: fit-content;

      > img {
        min-height: 1px; // IE bug fix so image doesn't stretch
        max-height: 600px; // For uncropped images
        width: auto;
      }

      > figcaption {
        &.banner__text--bottom-left,
        &.banner__text--top-left {
          left: calculate-modular-spacing(2, "xl");
        }

        &.banner__text--bottom-right,
        &.banner__text--top-right {
          right: calculate-modular-spacing(2, "xl");
        }
      }
    }
  }

  figcaption {
    @include box-shadow("shallow", 0);
    @include modular-spacing("padding", 1);
    background: rgba(255, 255, 255, 0.9);

    > * {
      &:first-child {
        @include margin(0 null null);
      }

      &:last-child {
        @include margin(null null 0);
      }
    }

    > p,
    > h2 {
      &:first-child {
        @include margin(-0.3em null null); // Compensate for extra space at top due to line height
      }
    }

    > p {
      line-height: $su-displays-line-height;

      &:last-child {
        @include margin(null null 0);
      }
    }

    > h2 {
      @include modular-spacing("margin", null null -3);
      @include type-d;
      color: get-color(brand--bright);
    }

    @include grid-media-only("xs") {
      @include padding(calculate-modular-spacing(0, "xs") map-get($su-screen-margins, "xs"));
    }

    @include grid-media-only("sm") {
      @include padding(calculate-modular-spacing(0, "sm") map-get($su-screen-margins, "sm"));
    }

    @include grid-media-only("md") {
      @include padding(calculate-modular-spacing(1, "md") map-get($su-screen-margins, "md"));
    }

    @include grid-media-min("lg") {
      position: absolute;
      width: 600px;
      max-width: 36%;
    }

    &.banner__text--top-left,
    &.banner__text--top-right {
      top: calculate-modular-spacing(2, "xl");
    }

    &.banner__text--bottom-left,
    &.banner__text--bottom-right {
      bottom: calculate-modular-spacing(2, "xl");
    }
  }

}

// When there is no banner image but there is text/HTML content in the banner region

.bannertext {
  @include margin(null auto);
  @include modular-spacing('padding', 5 null);
  @include rw-centered-column; // max site container width = 1500px at 2xl
  @include modular-typography(3);
  @include linear-gradient(to right, $su-color-purple, $su-color-lagunita);
  line-height: $su-base-line-height;
  font-weight: $su-font-bold;
  color: get-color(text--reverse);

  &.contentwidth {
    @include grid-media('xl') {
      $_padding: calc(#{map_get($su-screen-margins, 'xl')} + (100% - #{map_get($su-screen-margins, 'xl')} * 2) / 12);
      @include padding(null $_padding);
    }

    @include grid-media('2xl') {
      $_padding: calc(#{map_get($su-screen-margins, '2xl')} + (100% - #{map_get($su-screen-margins, '2xl')} * 2) / 6);
      @include padding(null $_padding);
    }

    // Breakpoint at 1700px
    @media only screen and (min-width: #{$su-screen-2xl + map_get($su-screen-margins, '2xl') * 2}) {
      $_padding: calc((100% - 1500px) / 2 + (1500px / 6));
      @include padding(null $_padding);
    }
  }

  > * {
    &:first-child {
      @include margin(0 null null);
    }

    &:last-child {
      @include margin(null null 0);
    }
  }

  a {
    color: get-color(link--reverse-alt);

    &:hover,
    &:focus {
      color: get-color(link--reverse-alt-hover);
    }
  }
}

body {
  > header {
    blockquote {
      @include margin(null null null 0);
    }
  }
}
