@charset "UTF-8";

select {
  font-size: 1em;

  @include grid-media('md') {
    font-size: 0.9em;
  }

  aside &,
  #pre-footer & {
    @include modular-typography(-1);
  }
}

label {
  @include margin(0 null null);
  font-weight: $su-font-semi-bold;
  color: get-color(text);
  display: block;

  &.screen-reader-text {
    @include hide-visually;
  }
}

