@charset "UTF-8";

// Styles for the Accordion Gutenberg

.c-accordion__item {
  @include margin(0);
  @include padding(1em null);
  border-bottom: 1px solid get-color(thin-line);

  .c-accordion__title {
    @include margin(0);
  }
}

.c-accordion__title {
  @include padding(null 3.8rem null null);
  font-size: 1.15em;
  color: get-color(link);

  strong {
    font-weight: $su-font-semi-bold;
  }

  &::after {
    display: block;
    background: $su-color-cloud;
    color: $su-color-black;
    border-radius: 50%;
    height: 3.3rem;
    width: 3.3rem;
    font-weight: $su-font-semi-bold;
    text-align: center;
    font-size: 1.5em;
    line-height: 3.3rem;

  }

  &:hover,
  &:focus {
    color: get-color(link--hover);
    text-decoration: underline;
    transition: color 0.2s ease-out;

    &::after {
      background: $su-color-bright-red;
      color: $su-color-white;
      transition: color 0.2s ease-out, background-color 0.3s ease-out;
    }
  }
}

.c-accordion__content {
  @include padding(1em null null);
}