@charset "UTF-8";

.intro-text {
  @include rw-intro-text;
}

// Posts
p + .tease {
  @include modular-spacing("margin", 2 null null);
}

.tease + .see-all {
  @extend %button-base;
  @include margin(null auto 1em);
  @include padding(1rem 8rem 1.15rem);
  display: block;
  width: fit-content;
  background-color: $su-color-white;
  box-shadow: $su-button-stroke $su-color-chocolate;
  color: $su-color-chocolate;

  &:hover,
  &:focus {
    background-color: $su-color-chocolate;
    color: $su-color-white;
  }

  &:hover {
    box-shadow: $su-button-stroke $su-color-chocolate;
  }

  &:focus {
    box-shadow: $su-button-stroke $su-color-black, $su-focus-shadow;
  }

  @include grid-media-only('xs') {
    width: 100%;
    @include padding(1rem 3rem 1.15rem);
  }

  @include ie-only {
    @include padding(null 2rem);
    width: 40%;
  }
}
