@charset "UTF-8";

// Sticky footer
@supports (grid-gap: 1em) {
  body {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    align-items: stretch;

    > header,
    > footer {
      flex-shrink: 0;
    }

    > main {
      flex: 1 0 auto;
    }
  }
}

html {
  scroll-behavior: smooth;
}

body > header,
body > header > section,
body > header > figure,
main,
main > section,
aside,
footer,
footer > section {
  position: relative;
}

// ---------------------------------------------------------------------------------------------------------------------
// z-index
// ---------------------------------------------------------------------------------------------------------------------
.su-brand-bar     {z-index: 200;}
.masthead         {z-index: 100;}
.masthead .su-lockup,
.sitename         {z-index: 10010;}
main, footer      {z-index: 90;}
main > aside      {z-index: 90;}

main,
#pre-footer {
  @include rw-centered-column;
}

main,
.pre-footer__content {
  display: grid;
  align-content: start;
  align-items: start;

  > * {
    min-width: 0;
  }
}

main {
  @include modular-spacing('padding', 5 null 8);
}

#pre-footer {
  @include modular-spacing('padding', 3 null);
}

.pre-footer__content {
  @include responsive-grid-gap;

  @include grid-media-max('md') {
    > div {
      @include margin(null null 1em);
    }
  }
}

main {
  grid-row-gap: 0; // For left-nav collapse we can't use row gap

  // left-nav
  > nav {
    grid-area: left-nav;
    align-self: start;
  }

  > section {
    grid-area: main;
  }
}

#sidebar--left {
  grid-area: sidebar-l;
}

#sidebar--right {
  grid-area: sidebar-r;
}

// On mobile everything is in one column
@include grid-media-max("sm") {
  main {
    grid-template-columns: 1fr;

    @include ie-only {
      > * {
        @include margin(null 0 null null); // IE
      }
    }

    > *:not(:last-child) {
      @include margin(null null calculate-modular-spacing(2, "xs"));
      @include padding(null null calculate-modular-spacing(2, "xs"));
      border-bottom: 2px solid color(brand--bright);
    }
  }

  .pre-footer__content {
    grid-template-columns: 1fr;

    @include ie-only {
      @include ie-autorow(8); // IE doesn't understand auto-placement with autofit
    }
  }
}

[tabindex="-1"]:focus {
  outline: 0;
}
