@charset "UTF-8";

// The categories widget shares a lot of common styles with the archive widget which can be found in _archive.scss
// Styles here are unique to the categories widget

.widget_categories {
  @extend %vertical-links--nested;

  ul ul li{
      border-top: 0;

    a {
      font-weight: $su-font-regular;
    }
  }
}

.wp-block-archives-dropdown,
.wp-block-categories-dropdown {
  @include margin(null null 1em);

  select {
    max-width: 100%;

    @include grid-media('sm') {
      max-width: 300px;
    }
  }
}
