@charset 'UTF-8';

// Gutenberg Media & Text block

@media only screen and (max-width: 600px) {
  .wp-block-media-text.is-stacked-on-mobile {

    &:not([class*="background-color"]) {
      & .wp-block-media-text__media {
        @include margin(null 8%);
      }
    }

    &[class*="background-color"] {
      & .wp-block-media-text__content {
        p:last-child {
          @include margin(null null 1em);
        }
      }

      &.has-media-on-the-right {
        & .wp-block-media-text__content {
          p:first-child {
            @include margin(1em null null);
          }
        }
      }
    }

    &.has-media-on-the-right {
      .wp-block-media-text__content {
        p:last-child {
          @include margin(null null 1em);
        }
      }
    }

    &:not(.has-media-on-the-right) {
      figure {
        @include margin(null null 1em);
      }
    }
  }
}

.wp-block-media-text {
  clear: both;

  @media only screen and (min-width: 601px) {
    &:not([class*="background-color"]) {
      & .wp-block-media-text__media {
        @include margin(null null null 8%);
      }

      &.has-media-on-the-right {
        & .wp-block-media-text__media {
          @include margin(null 8% null 0);
        }
      }
    }

    &[class*="background-color"] {
      & .wp-block-media-text__content {
        @include padding(8% null);
      }
    }
  }

  // Grid support for IE
  @include ie-only {
    display: -ms-grid;
    -ms-grid-rows: auto;
    -ms-grid-columns: 1fr 1fr;
    -ms-grid-row-align: center;

    .wp-block-media-text__content,
    figure {
      -ms-grid-row-align: center;
    }

    figure {
      -ms-grid-column: 1;
    }

    .wp-block-media-text__content {
      -ms-grid-column: 2;
    }

    &.has-media-on-the-right {
      .wp-block-media-text__content {
        -ms-grid-column: 1;
      }

      figure {
        -ms-grid-column: 2;
      }
    }

    @media only screen and (max-width: 600px) {
      &.is-stacked-on-mobile {
        &,
        &.has-media-on-the-right {
          -ms-grid-columns: 100%;

          figure,
          .wp-block-media-text__content {
            -ms-grid-column: 1;
          }
        }

        .wp-block-media-text__content {
          -ms-grid-row: 2;
        }

        &.has-media-on-the-right {
          .wp-block-media-text__content {
            -ms-grid-row: 1;
          }

          figure {
            -ms-grid-row: 2;
          }
        }
      }
    }
  }
}

.wp-block-media-text__content {
  p:last-child {
    @include margin(null null 0);
  }
}
