@charset "UTF-8";

// Centers a single cell in a container using padding on both sides for each breakpoint.
@mixin rw-centered-column($grid: $su-grid-media, $screens: $su-grid-screens, $margin: $su-screen-margins) {
  @each $key, $breakpoint in $grid {
    $_edge: map-get($margin, $key);

    @include grid-media($key) {
      @include padding(null $_edge);
    }
  }

  @media only screen and (min-width: #{$su-screen-2xl + map_get($su-screen-margins, '2xl') * 2}) {
    $_max_width_edge: calc((100% - #{$su-site-max-width}) / 2);
    @include padding(null $_max_width_edge);
  }
}