@charset "UTF-8";

// Mixin for writing media query for Redwood sidebar styles
// at the MD (2- and 3-column) and XL (3-column only) breakpoints

@mixin grid-media-narrow-sidebars {
  @include grid-media-only('md') {
    aside {
      @content;
    }
  }

  @media only screen and (min-width: #{$su-screen-xl}) and (max-width: #{$su-screen-xl + 100}) {
    .three-column aside {
      @content;
    }
  }
}
