@charset "UTF-8";

.error404 {
  main {
    @include modular-spacing('padding', 9 null null);
    color: get-color(text--high-contrast);
    background: url("/wp-content/themes/redwood/assets/img/dish404.jpg") #007bad right -10px/100% auto no-repeat;

    > section {
      > h1 {
        text-align: left;
      }
    }

    .su-site-search {
      @include margin(null null 3rem);
      max-width: 48rem;
    }

    @include grid-media-only('xs') {
      .btn {
        width: 100%;
      }

      .su-site-search {
        max-width: 100%;
      }
    }

    @include grid-media-max('sm') {
      @include padding(12.2rem null null); // 0.85 of desktop value (will go into Decanter later)
      background: url("/wp-content/themes/redwood/assets/img/dish404-small.jpg") #2a2924 right 0/100% auto no-repeat;
    }

    @include grid-media-between('sm','md') {
      background: url("/wp-content/themes/redwood/assets/img/dish404-medium.jpg") #2a2924 right 0/100% auto no-repeat;
    }
  }

  &.one-column:not(.left-nav) {
    main {
      @include grid-media-only('lg') {
        grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr ;
        grid-template-areas: "main main main main";
      }

      @include grid-media-only('xl') {
        grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
        grid-template-areas: ". main main main main main main main . . . .";
      }

      @include grid-media-only('2xl') {
        grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr;
        grid-template-areas: ". main main main . .";
      }
    }
  }
}
