@charset "UTF-8";

// .post styles are common to both single post and page layouts

.post {
  .img--featured {
    @include modular-spacing('margin', null null 3);
    @include margin(null auto);

    @include grid-media-min('md') {
      max-height: 60rem;
    }
  }

  > header {
    > h1 {
      @include margin(-0.22em null null);
    }
  }
}

.post > header > h1:first-child,
main > section > h1:first-of-type,
.author .post > header h3:first-child,
.archive .post > header h2:first-child {
  @include margin(-0.3em null null);
}

.page,
.single {
  .img--featured {
    @include grid-media-only("xs") {
      margin-top: -6.4rem;
      margin-left: -#{map_get($su-screen-margins, "xs")};
      max-width: calc(100% + #{2 * map_get($su-screen-margins, "xs")});
    }

    @include grid-media-only("sm") {
      margin-top: -6.4rem;
      margin-left: -#{map_get($su-screen-margins, "sm")};
      max-width: calc(100% + #{2 * map_get($su-screen-margins, "sm")});
    }
  }
}

.post > header,
main > section > h1 {
  @include grid-media-max("sm") {
    text-align: center;
  }
}

.post .author,
.post > header time,
.post .categories,
.tease .author,
.tease time,
.prev-next {
  @include rw-small-paragraph;
}

.post:not(.post--type-attachment) > header time,
.tease time {
  display: block;
}

.post .author,
.post > header time,
.post .categories,
.tease .author {
  @include modular-spacing('margin', null null -3);
  line-height: 1.2;
}

.post > header,
.tease .author {
  a {
    color: get-color(link--dark);
    font-weight: $su-font-bold;
    text-decoration: underline;

    &:hover,
    &:focus {
      color: get-color(link--dark-hover);
    }
  }
}

.post__content:not(:first-child),
.prev-next {
  @include modular-spacing("margin", 2 null null);
}

.post__content {
  + .tags,
  + .prev-next {
    @include modular-spacing("margin", 5 null null);
  }
}

.post,
.post__content,
.post__comments {
  > *:last-child {
    @include margin(null null 0);
  }
}

.post__content,
.bannertext {
  h2 {
    &:not(:first-child) {
      @include margin(1.8em null null);
    }
  }

  h3,
  h4,
  h5,
  h6 {
    &:not(:first-child) {
      @include margin(1.6em null null);
    }
  }

  h1 + h2,
  h2 + h3,
  h3 + h4,
  h4 + h5 {
    &:not(:first-child) {
      @include margin(1em null null);
    }
  }

  h2,
  h3,
  h4,
  h5,
  h6 {
    @include margin(null null 0.6em);
    clear: none;
  }
}

.post__content,
.tease {
  &::after {
    content: "";
    display: block;
    clear: both;
  }
}
