@charset "UTF-8";

// ol, ul, dl styles

dl {
  @include margin(1em null);
  @include padding(null null null 0);
  line-height: $su-base-line-height;
}

dt {
  @include margin(null null 0.2em);
  font-weight: $su-font-bold;
}

dd {
  @include margin(null null 1em 0);

  &:last-child {
    @include margin(null null 0);
  }
}

.post__content {
  > ul,
  > ol {
    @include padding(null null null 2em);
  }

  ol {
    ol {
      list-style-type: lower-alpha;

      ol {
        list-style-type: lower-roman;
      }
    }
  }
}

ul,
ol {
  &.unstyled {
    @include padding(null null null 0);
    list-style-type: none;
  }
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  + dl {
    @include margin(0 null null);
  }
}
