@charset "UTF-8";

// For Redwood at the low end of the MD (2- and 3-column) and XL (3-column) breakpoints
// the narrow sidebar requires some custom styling for the widgets.

@include grid-media-narrow-sidebars {
  .widget_info_box_widget {
    > span:first-of-type {
      font-size: 2.4em;
      width: 2em;
      text-align: left;
    }
  }

  .widget_categories {
    ul ul li {
      @include margin(null null null 1.4em);
    }
  }

  .widget_author_widget {
    .avatar {
      width: 100px;
      min-width: 100px;
    }
  }
}
