@charset "UTF-8";

// Styles for post comments and comment forms

.post__comments {
  @include modular-spacing('margin', 1 null null);
  @include modular-spacing('padding', 0 null null);

  > h2 {
    @include type-c;
    @include margin(1.4em null null);
  }

  ol .comment__reply-list {
    @include margin(null null null 1em);
  }

  input[type="text"],
  input[type="email"],
  textarea {
    @include margin(0);
    max-width: 100%;
  }

  label {
    clear: both;
  }

  input {
    height: 2.2em;

    &[type="hidden"] + p {
      display: flex;
      align-items: center;
      line-height: 1.2;
    }
  }

  &:not(.no-js) {
    .comment__reply-form {
      &[aria-hidden="true"] {
        display: none;
      }
    }
  }

  &.no-js {
    .comment__reply {
      display: none;
    }
  }
}

.comment__list {
  @include padding(null null null 0);

  > li {
    > figure,
    > form {
      border-left: 5px solid $su-color-driftwood;
    }
  }
}

.comment__list,
.comment__reply-list {
  list-style-type: none;
}

.comment__reply,
.comment__edit-link {
  text-decoration: none;

  &::after {
    @include margin(null null null 4px);
    font-family: "Font Awesome 5 Free";
    font-weight: 900;
  }
}

.comment__edit-link {
  @include margin(null null null 10px);

  &::after {
    content: "\f044";
  }
}

.comment__content {
  > *:last-child {
    @include margin(null null 0);
  }
}

.comment__body--avatar {
  .comment__content,
  .comment__reply {
    @include margin(null null null 70px);
  }
}

.comment__reply {
  @include padding(0);
  @include margin(1.4em null null);
  color: get-color(link);
  font-weight: $su-font-semi-bold;
  background-color: transparent;
  border: 0;
  cursor: pointer;

  &::after {
    content: "\f3e5";
  }

  &-close {
    &::after {
      content: "\f00d";
    }
  }

  &:hover,
  &:focus {
    color: get-color(link--hover);
    text-decoration: underline;
  }

  &:focus {
    box-shadow: none;
  }
}

.comment__avatar {
  @include margin(null 10px null null);
  width: 60px;
  border-radius: 50%;

  + p {
    @include margin(null null 0);

    @include grid-media-min("md") {
      @include padding(6px null null);
    }

    + label {
      @include margin(1em null null);
    }
  }

  p & {
    float: left;
    width: 40px;
  }
}

.comment__body {
  @include margin(null null 0);
  @include padding(1.6em);
  background-color: get-color(bg--alt);

  + .comment__reply-form {
    @include padding(null 1.6em 1.6em 1.6em);
    background: get-color(bg--alt);

    > h3 {
      @include type-d;
      @include padding(1em null null);
      border-top: 1px solid $su-color-driftwood;
    }
  }

  &--avatar {
    + .comment__reply-form {
      @include padding(null 1.6em 1.6em calc(1.6em + 70px));
    }
  }

  > header {
    @include modular-spacing('margin', null null 0);
    color: get-color(text--light);

    p {
      @include margin(null null 0);
    }

    a:not(.comment__edit-link) {
      color: get-color(link--dark);

      &:hover,
      &:focus {
        color: get-color(link--dark-hover);
      }
    }
  }
}

.comment__body header,
.post__comments label,
.post__comments form p,
.comment__reply {
  font-size: 0.9em;
}

.post__comments input,
.post__comments textarea {
  @include rw-small-paragraph;
}

.comment__form {
  + h2 {
    @include modular-spacing("margin", 2 null null);
  }
}

.comment__form,
.comment__reply-form {
  .required {
    &::after {
      content: "*";
      font-weight: $su-font-bold;
      color: get-color(brand--bright);
    }
  }

  button {
    @include margin(0.8em 1em null null);
    font-size: 1em;
  }

  div,
  input {
    + label {
      @include padding(1em null null);
    }
  }

  textarea {
    @include margin(null null 0.2em);

    + p {
      @include margin(null null 0);
    }
  }

  .form__field--half {
    @include grid-media-min("sm") {
      width: calc((100% - 20px) / 2);
      float: left;

      &:nth-of-type(odd) {
        @include margin(null 20px null null);
      }
    }

    @include grid-media-only("xs") {
      &:nth-of-type(even) {
        @include margin(1em null null);
      }
    }
  }
}

.comment__reply-form {
  textarea {
    height: 7em;
  }
}

@include grid-media-only("xs") {
  .comment__cancel {
    width: auto;
  }
}

@include grid-media-max("sm") {
  header .comment__avatar {
    @include margin(null null 10px);
    width: 40px;
  }

  .post__comments {
    .comment__content,
    .comment__reply {
      @include margin(null null null 0);
    }

    ol .comment__reply-list {
      @include margin(null null null 0.4em);
    }
  }

  .comment__body--avatar {
    + .comment__reply-form {
      @include padding(null null null 1.6em);
    }
  }
}

@include grid-media-min("sm") {
  .comment__avatar {
    float: left;
  }
}
