@charset "UTF-8";

// Styles for the homepage layout which has the option of displaying a header/banner image at the top of the <main> area
// The homepage banner code can be found in components/_banner.scss

.home {
  .post__content > p:first-child {
    @include margin(-0.4em null null);

    img {
      @include margin(0.4em null null); // Cancels out above if the first element is an image wrapped inside a p tag
    }
  }
}

