@charset 'UTF-8';

.wp-block-separator,
hr {
  border: 1px solid get-color(post-separator);
  border-width: 1px 0 0;
}

hr {
  @include margin(1.4em null);
}

.wp-block-separator {
  @include modular-spacing('margin', 3 null);

  &.is-style-wide {
    border-bottom-width: 0;
  }

  &.is-style-default {
    width: 20%;
    border-width: 2px 0 0;
  }

  &.is-style-dots {
    line-height: 0.2;
    font-weight: $su-font-bold;
  }
}