@charset 'UTF-8';

// Gutenberg columns

.wp-block-columns {

  @include grid-media-min("sm") {
    @include margin(null null 1em);
    display: grid;
    word-break: break-word;

    &,
    &.has-2-columns {
      grid-template-areas: "col-1 col-2";
      grid-template-columns: 1fr 1fr;
      grid-column-gap: $su-gutter-lg;
    }

    &.has-3-columns {
      grid-template-areas: "col-1 col-2 col-3";
      grid-template-columns: 1fr 1fr 1fr;
      grid-column-gap: $su-gutter-lg;
    }

    &.has-4-columns {
      grid-template-areas: "col-1 col-2 col-3 col-4";
      grid-template-columns: 1fr 1fr 1fr 1fr;
      grid-column-gap: $su-gutter-lg;
    }

    &.has-5-columns {
      grid-template-areas: "col-1 col-2 col-3 col-4 col-5";
      grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
      grid-column-gap: $su-gutter-md;
    }

    &.has-6-columns {
      grid-template-areas: "col-1 col-2 col-3 col-4 col-5 col-6";
      grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr;
      grid-column-gap: $su-gutter-md;
    }
  }

  // Add short dividers before and after columns block on mobile for this variant
  @include grid-media-max("xs") {
    &.content-chunk {
      display: block;

      &::before,
      &::after {
        @include margin(null auto);
        display: block;
        content: "";
        width: 40%;
        background-color: get-color(thin-line);
        height: 1px;
      }

      &::before {
        @include margin(2em null 1em);
      }

      &::after {
        @include margin(1em null 2em);
      }
    }
  }
}

.wp-block-column {

  // Gutenberg breakpoint
  @media (max-width: 599px) {
    @include margin(null null 1em);
  }

  > *:last-child {
    @include margin(null null 0);
  }

  @include grid-media('sm') {
    @include margin(null null 0);
  }

  // Overriding Gutenberg column margins
  @media (min-width: 600px) {
    &:not(:last-child),
    &:nth-child(odd) {
      @include margin(null 0 null null);
    }

    &:not(:first-child),
    &:nth-child(2n) {
      @include margin(null null null 0);
    }
  }

  @include grid-media-min("sm") {
    &:nth-of-type(1) {
      grid-area: col-1;
    }

    &:nth-of-type(2) {
      grid-area: col-2;
    }

    &:nth-of-type(3) {
      grid-area: col-3;
    }

    &:nth-of-type(4) {
      grid-area: col-4;
    }

    &:nth-of-type(5) {
      grid-area: col-5;
    }

    &:nth-of-type(6) {
      grid-area: col-6;
    }
  }

  > h2,
  > h3,
  > h4 {
    &:first-child {
      @include margin(-0.3em null null);
    }
  }
}

@include grid-media-min("md") {
  .one-column:not(.left-nav) {
    .wp-block-columns {
      @include margin(null -12.5%);
    }
  }
}
