@charset "UTF-8";

nav[aria-label="pagination"] {
  text-align: center;

  ul {
    @include list-unstyled;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }

  li {
    > * {
      @include padding(6px 12px);
      @include margin(null null 10px -1px);
      @include modular-typography(-1);
      display: inline-block;
      font-weight: $su-font-semi-bold;

      text-decoration: none;
      border: 1px solid get-color(thin-line);
    }

    &.disabled {
      > span {
        background: get-color(bg--alt);
        color: $su-color-cool-grey;
      }
    }

    > a:hover,
    > a:focus {
      background-color: get-color(brand--bright);
      color: get-color(text--reverse);
      border-color:  get-color(brand--bright);
    }

    &.current > span,
    > a:active {
      background-color: get-color(bg--dark);
      color: get-color(text--reverse);
      border-color:  get-color(bg--dark);
    }
  }
}
