@charset "UTF-8";

// Styles for the author widget and inline author info for the author pages

.widget_author_widget,
.author-info {
  @extend %widget-box;

  .avatar {
    @include margin(null auto);
    width: 120px;
    min-width: 120px;
    border-radius: 50%;
  }
}

.widget_author_widget,
.author-info__content {
  > *:last-child {
    @include margin(null null 0);
    @include padding(null null 0);
  }

  p {
   text-align: left;
  }

  i {
    @include margin(null 0.3em null null);
    color: $su-color-cool-grey;
  }

  a {
    font-weight: $su-font-semi-bold;
  }
}

.widget_author_widget .avatar,
.author-info__content > h2,
.author-info__content > h2 + p {
  @include modular-spacing("margin", null null 0);
}

// author widget

.widget_author_widget {
  text-align: center;

  > h2 {
    @include modular-spacing("margin", null null -1);
    text-transform: none;
    color: get-color(widget-header--alt);
  }

  a,
  p {
    @include rw-small-paragraph;
  }

  > a {
    font-size: 0.9em;
  }

  a + a {
    @include margin(null null null 1.4rem);
  }

  p {
    text-align: left;
  }

  p,
  .read-more {
    @include margin(1em null null);
  }

  .read-more {
    @include margin(null auto);
    font-weight: $su-font-semi-bold;
    max-width: -moz-available;
  }
}

aside .widget_author_widget h2 {
  @include type-d;
}

// Author pages
.author-info {
  @include modular-spacing("margin", null null 2);
  display: flex;
  background-color: get-color(bg--alt);

  > img {
    align-self: flex-start;
  }

  @include grid-media-between("sm", "md") {
    .avatar {
      width: 100px;
      min-width: 100px;
    }
  }
}

.author-info__content > h2,
.author-info + h2,
.author h1 + h2 {
  font-weight: $su-font-semi-bold;
  color: get-color(brand);
}

.author-info__content {
  width: 100%;

  > h2 {
    @include type-c;
    @include margin(-0.23em null null);
  }

  > p {
    @include small-paragraph;
    @include modular-spacing("margin", null null -3);

    i {
      @include margin(null 0.3em null null);
      color: $su-color-cool-grey;
    }
  }

  a {
    color: get-color(link);

    &:hover,
    &:focus {
      color: get-color(link--hover);
    }
  }

}

.avatar + .author-info__content {
  @include modular-spacing("padding", null null null 2);
}

@include grid-media-only("xs") {
  .author-info {
    flex-direction: column;

    h2 {
      text-align: center;
    }
  }

  .avatar {
    @include margin(null null calculate-modular-spacing(2,"xs"));

    + .author-info__content {
      @include padding(null null null 0);
    }
  }
}

@include grid-media-max("sm") {
  .author-info + h2 {
    text-align: center;
  }
}