@charset "UTF-8";

// Gutenberg Search widget

.wp-block-search {
  @include margin(null null 1.4em);
  position: relative;
  max-width: 40rem;

  @include grid-media-only('xs') {
    max-width: 100%;
  }

  &__input {
    @include padding(0.6rem 2rem);

    display: inline-block;
    height: 4rem;
    max-width: 100%;
    border-radius: 3rem;
    font-size: 1.6rem;

    &::placeholder {
      color: $su-color-black;
      opacity: 1; // For Firefox
    }
  }

  &__button {
    @include hide-text;
    position: absolute;
    right: 1.2rem;
    height: 25px;
    opacity: 0.6;
  }

  &__label {
    @include modular-spacing('margin', null null -1);
    font-weight: $su-font-bold;
    text-transform: uppercase;
    color: get-color(widget-header);
  }

  .wp-block-search__button {
    @include margin(0);
    @include padding(0);
    width: 24px;
    top: unset;
    bottom: 1.1rem;
    background: transparent url("#{$su-image-path}/icon-search.svg") no-repeat 0 0;

    &:hover,
    &:active,
    &:focus {
      background-color: transparent;
      opacity: 1;
    }

    &:active,
    &:focus {
      box-shadow: none;
    }
  }
}
