@charset "UTF-8";

// Styles shared by all (or most) widgets

#sidebar--left,
#sidebar--right,
.pre-footer__content {
  & > * {
    &:last-child {
      @include margin(null null 0);
    }
  }
}

#sidebar--left,
#sidebar--right {
  & > * {
    @include modular-spacing('margin-bottom', 2);
  }
}

.widget,
.well,
.wp-block-cover__inner-container {
  word-wrap: break-word;
  overflow-wrap: break-word;
  word-break: break-word;
}

.widget,
.well {
  @include rw-small-paragraph;
  line-height: $su-base-line-height;

  @supports(grid-gap: 1em) {
    width: 100%;
  }

  > h2 {
    @include modular-spacing('margin', null null -1);
    margin-top: -0.24em; // Compensate for line height
    color: get-color(widget-header);

    article & {
      @include type-d;
    }

    #pre-footer & {
      font-size: 1.8rem;
      color: get-color(widget-header--alt);
    }
  }

  > *:last-child,
  .textwidget > *:last-child {
    @include margin(null null 0);
  }

  > ul > li:first-of-type,
  > ul > li:first-of-type > a {
    @include padding(0 null null);
  }

  > ul > li:last-of-type,
  > ul > li:last-of-type > a {
    @include padding(null null 0);
  }
}

.widget {

  > h2 {
    aside & {
      @include type-e;
    }
  }
}

#pre-footer {
  .widget:not(.widget_custom_html):not(.widget_media_gallery) {
    @supports(grid-gap: 1em) {
      @include margin(null auto);
    }

    max-width: 724px;
  }
}
