@charset "UTF-8";

.three-column {
  main {
    @include grid-media-max("sm") {
      grid-template-areas: "main" "sidebar-l" "sidebar-r";
    }

    // Right sidebar move under left sidebar
    @include grid-media-min("md") {
      grid-template-areas: "sidebar-l main main" "sidebar-r main main";
      grid-template-columns: 1fr 1fr 1fr;
      grid-template-rows: auto 1fr;
      grid-column-gap: $su-gutter-md;
    }

    @include grid-media-min("lg") {
      -ms-grid-columns: 1fr $su-gutter-lg 1fr $su-gutter-lg 1fr;
      grid-column-gap: $su-gutter-lg;
    }

    @include grid-media-min("xl") {
      grid-template-areas: "sidebar-l main main sidebar-r";
      grid-template-columns: 1fr 1fr 1fr 1fr;
      grid-template-rows: auto;
      grid-column-gap: $su-gutter-xl;
    }

    @include grid-media-min("2xl") {
      -ms-grid-columns: 1fr $su-gutter-2xl 1fr $su-gutter-2xl 1fr $su-gutter-2xl 1fr;
      grid-column-gap: $su-gutter-2xl;
    }
  }

  @include grid-media-between("md", "lg") {
    > nav, #sidebar--left {
      @include margin(null null calculate-modular-spacing(2, "md"));
      @include padding(null null calculate-modular-spacing(2, "md"));
      border-bottom: 2px solid color(brand--bright);
    }
  }
}
