@charset "UTF-8";

// Common styles shared between different WordPress blocks and elements

.wp-block-gallery,
.wp-block-cover,
.wp-block-embed.is-type-video,
.wp-block-video {
  &.alignright,
  &.alignleft {
    max-width: 100%;

    @include grid-media-min('lg') {
      max-width: 67%;
    }
  }
}

figure,
img {
  &.alignnone {
    @include margin(null auto);
    max-width: 100%;
  }
}

@include grid-media-min('lg') {
  figure,
  img,
  .wp-block-gallery,
  .wp-block-cover,
  .wp-block-embed.is-type-video,
  .wp-block-video {
    &.alignleft {
      @include margin(null 1em null null);
    }

    &.alignright {
      @include margin(null null null 1em);
    }

    &.aligncenter {
      max-width: 75%;
    }
  }

  .wp-block-pullquote {
    @include margin(null auto);
    max-width: 75%;

    .wp-block-column & {
      max-width: 100%;
    }
  }

  // Image outset for one column layout
  .one-column:not(.left-nav) {
    figure,
    img,
    .wp-block-gallery,
    .wp-block-cover,
    .wp-block-embed,
    .wp-block-video,
    .wp-block-pullquote {
      &.alignright {
        @include margin(null -12.5% null null);
      }

      &.alignleft {
        @include margin(null null null -12.5%);
      }
    }
  }
}

// Caption
figure.wp-block-image,
figure.wp-caption,
.post--type-attachment figure {
  display: table;

  figcaption {
    display: table-caption;
    caption-side: bottom;
  }
}

.wp-block-image,
.wp-caption,
.wp-block-video,
.wp-block-audio,
.post--type-attachment,
.wp-block-embed {
  figcaption {
    @include margin(null null 0);
    font-size: 0.84em;
    color: get-color(caption);
    text-align: left;
  }
}

figure.wp-caption {
  width: auto!important;
}

.wp-block-rss,
.wp-block-latest-posts,
.wp-block-latest-comments {
  time {
    color: $su-color-clay;
    font-size: 0.9em;
  }
}

.wp-block-rss__item-excerpt {
  @include margin(0.2em null null);
}

.wp-block-latest-comments__comment-excerpt {
  p {
    font-size: 1.7rem;
    line-height: $su-base-line-height;

    @include grid-media-max('md') {
      font-size: 1.5rem;
    }
  }
}
