@charset 'UTF-8';

// Quotes and Pullquotes

blockquote {
  line-height: $su-base-line-height;

  &::before {
    @include margin(null null null -2px);
    font-size: 2.6em;
    line-height: 0.66;
    content: '\201C';
    display: block;
    color: get-color(quotes);
    font-weight: $su-font-bold;
  }

  p {
    @include margin(null null 0.5em);
    @include serif;

    &:first-child {
      @include margin(-8px null null);
    }
  }

  cite {
    display: block;
    position: relative;
    top: 5px;
    font-style: italic;

    &::first-line {
      font-weight: $su-font-bold;
    }
  }
}

blockquote {
  @include margin(null 0 null null);
}

main {
  blockquote,
  .wp-block-quote.is-style-large {
    @include margin(1.4em 0 1.4em null);
    @include modular-spacing("margin", null null null 2);
    @include modular-spacing("padding", null null null 1);
    border-left: 5px solid get-color(brand--bright);
  }
}

.wp-block-quote.is-large,
.wp-block-quote.is-style-large,
.wp-block-pullquote blockquote {
  &::before {
    @include margin(null null null -4px);
    font-size: 10rem;
  }
}

.wp-block-quote.is-large,
.wp-block-quote.is-style-large,
.wp-block-pullquote blockquote,
.wp-block-pullquote.alignleft,
.wp-block-pullquote.alignright {
  cite,
  footer {
    @include modular-typography(1);
  }

  p {
    @include modular-typography(2);
    font-weight: $su-font-bold;
    line-height: 1.5;
  }
}

.wp-block-quote {
  &.is-large,
  &.is-style-large {
    p {
      font-style: normal;
    }

    cite {
      top: 7px;
      text-align: left;
    }
  }
}

.wp-block-pullquote {
  @include modular-spacing("padding", 3 1 2);
  border-top: 5px solid get-color(brand--bright);
  border-bottom: 5px solid get-color(brand--bright);

  &.is-style-default {
    border-color: get-color(brand--bright)!important;
  }

  &:not(.alignleft):not(.alignright) {
    clear: both;
  }

  blockquote {
    @include margin(0);
    @include padding(null null null 0);
    border-left: 0;

    p:first-child {
      @include margin(-0.7em null null);
    }
  }

  &.alignright,
  &.alignleft {
    max-width: 100%;

    @include grid-media-min('lg') {
      max-width: 50%;
    }
  }
}
