@charset "UTF-8";

// Area above the global footer where widgets go

#pre-footer {
  background: get-color(prefooter-bg);
}

.su-local-footer__header {
  @include modular-spacing('padding', null null 3);
}

.pre-footer__content {
  @include grid-media('md') {
    grid-template-columns: repeat(auto-fit, minmax(27rem, 1fr));
  }

  @include grid-media-between('md', 'xl') {
    // IE doesn't understand auto-fit so we support up to 8 widgets by laying them out manually
    @include ie-autocolumn(3);
    -ms-grid-columns: 1fr 1fr 1fr;
    -ms-grid-rows: auto;

    > div:nth-child(4),
    > div:nth-child(5),
    > div:nth-child(6) {
      -ms-grid-row: 2;
    }

    > div:nth-child(7),
    > div:nth-child(8) {
      -ms-grid-row: 3;
    }

    > div:nth-child(4) {
      -ms-grid-column: 1;
    }

    > div:nth-child(5) {
      -ms-grid-column: 2;
    }

    > div:nth-child(6) {
      -ms-grid-column: 3;
    }

    > div:nth-child(8) {
      -ms-grid-column: 2;
    }
  }

  @include grid-media('2xl') {
    // IE doesn't understand auto-fit so we support up to 8 widgets by laying them out manually
    @include ie-autocolumn(4);
    -ms-grid-columns: 1fr 1fr 1fr 1fr;
    -ms-grid-rows: auto;

    > div:nth-child(5),
    > div:nth-child(6),
    > div:nth-child(7),
    > div:nth-child(8) {
      -ms-grid-row: 2;
    }

    > div:nth-child(4) {
      -ms-grid-row: 1;
      -ms-grid-column: 4;
    }

    > div:nth-child(5) {
      -ms-grid-column: 1;
    }

    > div:nth-child(6) {
      -ms-grid-column: 2;
    }

    > div:nth-child(7) {
      -ms-grid-column: 3;
    }

    > div:nth-child(8) {
      -ms-grid-column: 4;
    }
  }

  a {
    color: get-color(prefooter-link);

    &:hover,
    &:focus {
      color: get-color(prefooter-link--hover)
    }
  }
}
