@charset "UTF-8";

.widget_info_box_widget {
  display: flex;
  flex-direction: row;
  align-items: flex-start;

  // FontAwesome icon
  > span:first-child {
    @include margin(null auto);
    font-size: 2.8em;
    width: 2.4em;
    color: $su-color-driftwood;
    text-align: center;

    #pre-footer & {
      font-size: 2.8em;
      width: 1.9em;
      text-align: left;
    }
  }

  > .info-box__content {
    width: 100%;
    line-height: $su-base-line-height;

    h2 {
      @include modular-spacing('margin', null null -2);
      @include type-d;
      @include margin(-0.24em null null);
      color: get-color(widget-header--alt);

      #pre-footer & {
        font-size: 1.8rem;
      }
    }
  }
}

.info-box__content {
  > p,
  > ul,
  > ol,
  > dl {
    &:last-child {
      @include margin(null null 0);
    }
  }
}
