@charset 'UTF-8';

// Gutenberg buttons, button shortcode, general site buttons
// Backward compatibility for buttons on sites using old themes

.wp-block-button,
.button,
.btn {
  @include margin(null null 1em);

  &.alignright,
  &.alignleft {
    clear: both;
    float: none;
    max-width: 100%;
  }
}

.wp-block-button__link {
  border-radius: 0;
}

.button,
.btn {
  &.alignright {
    text-align: right;
  }

  &.aligncenter {
    text-align: center;
  }
}

aside button,
footer button,
.post__content button,
form button,
.button a,
.btn,
.wp-block-button__link,
input[type="submit"] {
  @include button-primary;
}

.wp-block-button.is-style-outline a,
button[type="reset"],
.button--outline a {
  @include button-secondary;
}

.button--fullwidth a,
.button--fullwidth.is-style-outline a {
  width: 100%;
}

.button--big a {
  @include button-big;
}

.button--one a,
.btn-one,
.post__content .btn-one {
  background-color: get-color(button--one);
}

.button--two a,
.btn-two,
.post__content .btn-two {
  background-color: get-color(button--two);
}

.button--three a,
.btn-three,
.post__content .btn-three {
  background-color: get-color(button--three);
}

.button--primary a,
.btn-primary,
.post__content .btn-primary {
  background-color: get-color(button--primary);
}

.btn-su-alert a,
.btn-su-alert,
.post__content .btn-su-alert {
  background-color: $su-color-warm-grey;
}

.btn-u,
.post__content .btn-u {
  background-color: $su-color-sandstone;
  color: $su-color-warm-grey;

  &:hover,
  &:focus {
    color: $su-color-white;
  }
}

.button--primary a,
.button--three a,
.btn {
  &:hover,
  &:focus,
  &:active {
    background-color: #000;
  }
}
