@charset "UTF-8";

.widget_social-icons {
  a {
    @include margin(0.3em 0.8em 0.8em null);
    display: inline-block;
    width: 2.2em;
    text-align: center;
    font-weight: $su-font-regular;
    color: get-color(social-icon);

    @each $key, $brand in $social-brands {
      &[aria-label='#{$key}'] {
        &:hover,
        &:focus {
          color: get-color($key, $social-brands);
        }
      }
    }
  }
}