@charset "UTF-8";

.widget_hero_image_widget {
  @extend %widget-box;
  @include padding(0);

  figure {
    @include margin(0);
  }

  figcaption {
    @extend %accent-stroke;
    @include modular-spacing('padding', 2);
    @include margin(0 null null);
    line-height: $su-base-line-height;

    > p {
      display: inline;
    }

    > h2,
    > h4 { // Support markup of old themes
      @include type-e;
      @include margin(null 0.6em 0 null);
      display: inline-block;
      color: get-color(widget-header--alt);
    }

    > *:last-child {
      @include padding(null null 0);
      @include margin(null null 0);
    }
  }
}

@include grid-media-narrow-sidebars {
  .widget_hero_image_widget {
    @include padding(0);

    figcaption {
      @include padding(2.8rem);
    }
  }
}