@charset "UTF-8";

%vertical-links {
  ul {
    @include list-unstyled;
  }

  li {
    @include margin(0);
    @include padding(0.7em null);
    display: block;
    overflow-wrap: break-word;

  }

  li a {
    text-decoration: none;
  }
}

// Adds a full-width line between each link
%vertical-links--line {
  li {
    border-top: 1px solid get-color(thin-line);
  }
}

// Works with non-nested ul
%vertical-links--centered-line-short {
  ul {
    text-align: center;
  }

  li:before {
    @include margin(null auto);
    display: block;
    content: "";
    width: 50%;
    border-top: 1px solid get-color(thin-line);
  }
}

// Styles for link lists with more than 1 level
%vertical-links--nested {
  ul ul {
    li {
      @include margin(null null null 2em);
      @include padding(1em null 0);

      &:last-child {
        border-bottom: 0;
      }
    }
  }
}
