@charset "UTF-8";

// Widget Categories and Archive share common styles. Categories has some extra styles in _categories.scss.

.widget_archive,
.widget_categories {
  @extend %widget-box;
  @extend %accent-stroke;
  @extend %vertical-links;
  @extend %vertical-links--line;

  > h2 {
    @include modular-spacing("margin", null null 0);

    #pre-footer & {
      color: get-color(widget-header);
    }
  }

  li a {
    color: get-color(link--dark);

    &:hover,
    &:focus {
      color: get-color(link--dark-hover);
      text-decoration: underline;
    }
  }

  > ul li:first-of-type {
    border-top: 0;
  }

  > ul > li > a {
    font-weight: $su-font-semi-bold;
  }
}
