@charset "UTF-8";

.prev-next {
  position: relative;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-column-gap: $su-gutter-md;

  @include grid-media-min("lg") {
    grid-column-gap: $su-gutter-xl;
  }

  a {
    display: block;
    text-decoration: none;
    width: fit-content;

    &::before {
      position: absolute;
      top: 0;
      font-family: "Font Awesome 5 Free";
      font-weight: 900;
      font-size: 16px;
      color: get-color(brand);
      transition: left 0.2s ease-out, right 0.2s ease-out;

      @include grid-media-max("sm") {
        font-size: 16px;
      }
    }

    &:hover,
    &:focus {
      &::before {
        color: get-color(link--dark);
      }
    }

    &[rel="prev"] {
      grid-column: 1;

      &::before {
        content: "\f0a8";
        left: 0;
      }

      &:hover,
      &:focus {
        &::before {
          left: -4px;
        }
      }

      > p {
        @include padding(null null null 2rem);
      }
    }

    &[rel="next"] {
      grid-column: 2;
      justify-self: end;
      text-align: right;

      &::before {
        content: "\f0a9";
        right: 0;
      }

      &:hover,
      &:focus {
        &::before {
          right: -4px;
        }
      }

      > p {
        @include padding(null 2rem null null);
      }
    }

    p {
      &:first-of-type {
        @include margin(null null 3px);
        font-weight: $su-font-bold;
        color: get-color(brand);
        line-height: $su-displays-line-height;
        text-transform: uppercase;

        @include grid-media-min("md") {
          @include margin(-2px null null);
        }
      }

      &:last-of-type {
        @include margin(null null 0);
        color: get-color(text);
        font-weight: $su-font-regular;
      }
    }

    &:hover,
    &:focus {
      p:first-of-type {
        color: get-color(link--alt-hover);
      }

      p:last-of-type {
        text-decoration: underline;
      }
    }
  }
}
