@charset "UTF-8";

a {
  transition: color 0.2s ease-out, background-color 0.2s ease-out;
}

.read-more {
  display: block;
  line-height: $su-base-line-height;
  max-width: fit-content;
  text-decoration: none;

  &::after {
    @include margin(null null -1px 0.4em);
    content: '\00BB';
    position: relative;
    left: 0;
    display: inline;
    transition: left 0.2s ease-in-out;
  }

  &:hover,
  &:focus {
    &::after {
      left: 0.2em;
    }
  }
}

article {
  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    a {
      color: get-color(link--dark);
      font-weight: $su-font-bold;

      &:hover,
      &:focus {
        text-decoration: underline;
        color: get-color(link--dark-hover)
      }
    }
  }
}

article {
  a,
  p a,
  span a {
    font-weight: $su-font-semi-bold;
  }
}

.widget {
  a,
  p a,
  span a {
    font-weight: $su-font-regular;
  }

  .read-more {
    font-weight: $su-font-semi-bold;
  }
}
