@charset "UTF-8";

.password-form {
  button {
    height: 4.8rem; // Same as Decanter input field height
  }

  @include grid-media-only('xs') {
    button[type="submit"] {
      @include margin(1em null null);
      display: block;
      width: 100%;
    }
  }

  @include grid-media("sm") {
    input[type="password"] {
      display: inline-block;
      max-width: 30rem;
    }

    button {
      @include margin(null null null 0.6em);
      position: relative;
      top: -1px;
    }
  }
}
