@charset "UTF-8";

.widget_page_nav_widget,
.widget_nav_menu {
  @extend %nav--light;
  display: block;
  position: relative;

  h2 {
    @include modular-spacing("margin", null null 0);
  }

  ul {
    @extend %nav-menu;
    @include padding(null null null 0);
    background-color: transparent;
    width: auto;
  }

  li {
    @extend %nav-item;

    a {
      @extend %nav-item-link;
      @include padding(null 1.6rem null null);
    }

    ul {
      @include margin(null null null 2rem);
    }
  }
}

.widget_nav_menu .sub-menu,
.widget_page_nav_widget > ul ul {
  display: flex;
}

.widget_nav_menu .sub-menu,
.widget_page_nav_widget > ul > li > ul > li {
  a {
    @include padding(0.9rem null 1.4rem);
    border-top: 0;
  }
}

.widget_page_nav_widget .current_page_item,
.widget_nav_menu .current-menu-item {
  @extend %nav-item--current;

  > a {
    color: color(link--active, $su-nav-colors--light);

    &,
    &:hover,
    &:focus {
      &::before {
        background-color: color(border--active, $su-nav-colors--light);
      }
    }
  }
}

.widget_nav_menu > div,
.widget_page_nav_widget {
  > ul {
    border-bottom: 1px solid color(divider, $su-nav-colors--light);

    > li > ul > li {
      > a {
        font-size: 1.7rem;
      }
    }
  }
}

.widget_page_nav_widget {
  > ul {
    > li {
      &:first-of-type {
        > a {
          @include padding(1.6rem null null);
        }
      }

      &:last-of-type {
        > a {
          @include padding(null null 1.6rem);
        }
      }
    }
  }
}
