@charset "UTF-8";

// More finetuning for small paragraph font for Redwood

@mixin rw-small-paragraph {
  font-size: 1.8rem;

  @include grid-media-between('lg', 'xl') {
    font-size: 1.7rem;
  }

  @include grid-media-only('md') {
    font-size: 1.66rem;
  }

  @include grid-media-max('sm') {
    font-size: 1.6rem;
  }
}
